<template>
    <div>
        <header>
            <h1>Circle drawing</h1>
        </header>
        <figure id="diagram">
            <svg viewBox="0 0 500 200">
                <rect v-for="({ q, r }, index) in positions" :key="index" :x="q * scale + 1 / 2" :y="r * scale + 1 / 2"
                    :width="scale - 1" :height="scale - 1"
                    :fill="insideCircle(center, { q, r }, radius) ? 'hsl(0 30% 60%)' : 'hsl(250 10% 93%)'" />

                <circle fill="yellow" @touchstart.prevent="" @dragstart.prevent="" @pointerdown.left="pointerDown"
                    @pointerup="pointerUp" @pointercancel="pointerUp" @pointermove="moveCenter" :r="0.4 * scale"
                    :cx="(center.q + 1 / 2) * scale" :cy="(center.r + 1 / 2) * scale" />
            </svg>

            <div style="display: flex; flex-direction: column; justify-content: center; align-items: flex-start;">
                <label>
                    col = <input v-model.number="center.q" type="range" min="0" :max="gridCols - 1" />
                </label>
                <label>
                    row = <input v-model.number="center.r" type="range" min="0" :max="gridRows - 1" />
                </label>
                <label>
                    radius =
                    <input v-model.number="radius" type="range" min="1" max="8" step="0.1" />
                    <input v-model.number="radius" type="number" min="1" max="8" step="0.1" size="3" />
                </label>
            </div>
        </figure>
        <footer>Last updated 1 Jan 2023.</footer>
    </div>
</template>

<script>
import * as d3 from "d3";

function convertPixelToSvgCoord(event) {
    const svg = event.currentTarget.ownerSVGElement;
    let p = svg.createSVGPoint();
    p.x = event.clientX;
    p.y = event.clientY;
    p = p.matrixTransform(svg.getScreenCTM().inverse());
    return p;
}

function clamp(value, lo, hi) {
    return value < lo ? lo : value > hi ? hi : value;
}

export default {
    name: "D3Learning",
    data() {
        return {
            scale: 20,
            gridCols: 25,
            gridRows: 10,
            positions: [],
            center: { q: 5, r: 4 },
            radius: 4,
        };
    },
    mounted() {
        // this.draw()
        for (let q = 0; q < this.gridCols; q++) {
            for (let r = 0; r < this.gridRows; r++) {
                this.positions.push({ q, r });
            }
        }
    },
    methods: {
        insideCircle(center, tile, radius) {
            let dq = center.q - tile.q,
                dr = center.r - tile.r;
            let distance = dq * dq + dr * dr;
            return distance <= radius * radius;
        },

        pointerDown(event) {
            this.dragging = true;
            event.currentTarget.setPointerCapture(event.pointerId);
        },

        pointerUp(event) {
            this.dragging = false;
            console.log(event);
        },

        moveCenter(event) {
            if (!this.dragging) return;
            let { x, y } = convertPixelToSvgCoord(event);
            let q = Math.round(x / this.scale - 1 / 2);
            let r = Math.round(y / this.scale - 1 / 2);
            this.center.q = clamp(q, 0, this.gridCols - 1);
            this.center.r = clamp(r, 0, this.gridRows - 1);
        },

        draw() {
            const svg = d3
                .select("div")
                .append("svg")
                .attr("width", 500)
                .attr("height", 500);

            svg
                .append("circle")
                .attr("cx", 250)
                .attr("cy", 250)
                .attr("r", this.radius)
                .attr("fill", "red");

            this.$watch("radius", (newVal, oldVal) => {
                console.log(oldVal);
                svg
                    .select("circle")
                    .transition()
                    .duration(1000)
                    .ease(d3.easeBounce)
                    .attr("r", newVal);
            });
        },
    },
};
</script>

<style lang="scss" scoped>
svg {
    display: block;
    background: white;
}
</style>